<template>
    <div>
        <h1 class="text-center">合规产品查询</h1>
        <van-form label-width="170px" @submit="onSubmit">
            <van-field
                    v-model="formData.type"
                    label="产品类型"
                    placeholder="请选择产品类型"
                    readonly
                    @focus="isShowType = true"
            >
            </van-field>
            <van-field
                    v-model="formData.brand"
                    label="产品品牌"
                    placeholder="请选择产品品牌"
                    readonly
                    @focus="isShowBrand = true"
            />
            <van-field
                    v-model.trim="formData.keyword"
                    label="关键字（型号/厂家）"
                    placeholder="请输入型号/厂家查询"
            />
            <div style="margin: 16px;">
                <van-button block type="primary" @click="search">查询</van-button>
            </div>
        </van-form>
        <van-popup v-model="isShowType" position="bottom" safe-area-inset-bottom>
            <van-picker
                    show-toolbar
                    title="请选择产品类型"
                    :columns="typeList"
                    @confirm="onTypeChoose"
                    @cancel="isShowType = false"
                    ref="type"
            />
        </van-popup>
        <van-popup v-model="isShowBrand" position="bottom" safe-area-inset-bottom>
            <van-picker
                    show-toolbar
                    title="请选择产品品牌"
                    :columns="brandList"
                    @confirm="onBrandChoose"
                    @cancel="isShowBrand = false"
                    ref="brand"
            />
        </van-popup>
    </div>
</template>

<script>
import brandList from './BrandData'
import typeList from './TypeData'
import searchData from './SearchData'
import {cloneDeep} from '@/util'

export default {
    name: 'SearchPage',
    data() {
        return {
            isShowType: false,
            isShowBrand: false,
            formData: {
                type: '',
                brand: '',
                keyword: ''
            },
            typeList,
            brandList
        }
    },
    watch: {
        isShowType(val) {
            if (!val) {
                return
            }
            let index = this.typeList.indexOf(this.formData.type)
            if (index === -1) {
                return
            }
            this.$nextTick(() => {
                this.$refs.type.setIndexes([index])
            })
        },
        isShowBrand(val) {
            if (!val) {
                return
            }
            let index = this.brandList.indexOf(this.formData.brand)
            if (index === -1) {
                return
            }
            this.$nextTick(() => {
                this.$refs.brand.setIndexes([index])
            })
        }
    },
    methods: {
        onSubmit() {

        },
        onTypeChoose(val) {
            this.formData.type = val
            this.isShowType = false
        },
        onBrandChoose(val) {
            this.formData.brand = val
            this.isShowBrand = false
        },
        search() {
            const {type, brand, keyword} = this.formData
            const re = new RegExp(keyword, 'i')
            let result = cloneDeep(searchData)
            if (type && type !== '全部') {
                result = result.filter(item => item.type === type)
            }
            if (brand && brand !== '全部') {
                result = result.filter(item => item.brand === brand)
            }
            if (keyword) {
                result = result.filter(({model, brandName}) => re.test(model) || re.test(brandName))
            }
            if (!result.length) {
                this.$toast('没有找到相关产品')
                return
            }
            this.$emit('onSearch', {
                searchQuery: cloneDeep(this.formData),
                result
            })
        }
    }
}
</script>

<style scoped>
    h1 {
        margin-bottom: .4rem;
    }
    .van-cell {
        display: block;
    }
    /deep/ .van-cell__value {
        padding: .1rem 0;
    }
</style>
