<template>
    <div>
        <div class="header">
            <div @click="canChooseBrand && (isShowBrand = true)">{{formData.brand || '品牌'}}</div>
            <div @click="canChooseType && (isShowType = true)">{{formData.type || '类型'}}</div>
            <div @click="isShowBrandName = true">{{formData.brandName || '厂家'}}</div>
        </div>
        <van-list
                v-model="loading"
                :finished="finished"
                finished-text="没有更多了"
                @load="onLoad"
        >
            <van-cell-group class="list_detail" v-for="(item, key) in list" :key="key">
                <van-cell :border="false" title="品牌" :value="item.brand" />
                <van-cell :border="false" title="类型" :value="item.type" />
                <van-cell :border="false" title="型号" :value="item.model" />
                <van-cell :border="false" title="厂家" :value="item.brandName" />
                <van-cell :border="false" title="报修电话" :value="item.phone" />
            </van-cell-group>
        </van-list>
        <van-popup v-model="isShowBrand" position="bottom" safe-area-inset-bottom>
            <van-picker
                    show-toolbar
                    title="请选择产品品牌"
                    :columns="brandList"
                    @confirm="onBrandChange"
                    @cancel="isShowBrand = false"
                    ref="brand"
            />
        </van-popup>
        <van-popup v-model="isShowType" position="bottom" safe-area-inset-bottom>
            <van-picker
                    show-toolbar
                    title="请选择产品类型"
                    :columns="typeList"
                    @confirm="onTypeChange"
                    @cancel="isShowType = false"
                    ref="type"
            />
        </van-popup>
        <van-popup v-model="isShowBrandName" position="bottom" safe-area-inset-bottom>
            <van-picker
                    show-toolbar
                    title="请选择厂家"
                    :columns="brandNameList"
                    @confirm="onBrandNameChange"
                    @cancel="isShowBrandName = false"
                    ref="brandName"
            />
        </van-popup>
    </div>
</template>

<script>
import {cloneDeep} from '@/util'

export default {
    name: 'SearchResult',
    props: {
        searchQuery: {
            type: Object,
            default: () => ({})
        },
        result: {
            type: Array,
            default: () => []
        }
    },
    data() {
        return {
            isShowType: false,
            isShowBrand: false,
            isShowBrandName: false,
            loading: false,
            list: [],
            cloneResult: [],
            current: 0,
            formData: {},
        }
    },
    computed: {
        brandList() {
            return ['全部', ...new Set(this.result.map(item => item.brand))]
        },
        typeList() {
            return ['全部', ...new Set(this.result.map(item => item.type))]
        },
        brandNameList() {
            return ['全部', ...new Set(this.result.map(item => item.brandName))]
        },
        canChooseType() {
            let val = this.searchQuery.type
            return !val || val === '全部'
        },
        canChooseBrand() {
            let val = this.searchQuery.brand
            return !val || val === '全部'
        },
        finished() {
            return this.list.length === this.cloneResult.length
        }
    },
    watch: {
        searchQuery: {
            immediate: true,
            handler(val) {
                this.formData = cloneDeep(val)
            }
        },
        result: {
            immediate: true,
            handler(val) {
                this.cloneResult = cloneDeep(val)
            }
        },
        cloneResult: {
            immediate: true,
            handler(val) {
                this.current = 0
                this.list = val.slice(0, 10)
            }
        },
        isShowType(val) {
            if (!val) {
                return
            }
            let index = this.typeList.indexOf(this.formData.type)
            if (index === -1) {
                return
            }
            this.$nextTick(() => {
                this.$refs.type.setIndexes([index])
            })
        },
        isShowBrand(val) {
            if (!val) {
                return
            }
            let index = this.brandList.indexOf(this.formData.brand)
            if (index === -1) {
                return
            }
            this.$nextTick(() => {
                this.$refs.brand.setIndexes([index])
            })
        },
        isShowBrandName(val) {
            if (!val) {
                return
            }
            let index = this.brandNameList.indexOf(this.formData.brandName)
            if (index === -1) {
                return
            }
            this.$nextTick(() => {
                this.$refs.brandName.setIndexes([index])
            })
        }
    },
    methods: {
        onLoad() {
            setTimeout(() => {
                this.current++
                this.list = [...this.list, ...this.cloneResult.slice(this.current * 10, (this.current + 1) * 10)]

                // 加载状态结束
                this.loading = false
            }, 500)
        },
        filterData() {
            let result = this.result
            const {type, brand, brandName} = this.formData
            if (type && type !== '全部') {
                result = result.filter(item => item.type === type)
            }
            if (brand && brand !== '全部') {
                result = result.filter(item => item.brand === brand)
            }
            if (brandName && brandName !== '全部') {
                result = result.filter(item => item.brandName === brandName)
            }
            return result
        },
        onTypeChange(val) {
            this.formData.type = val
            this.isShowType = false
            this.cloneResult = cloneDeep(
                this.filterData()
            )
        },
        onBrandChange(val) {
            this.formData.brand = val
            this.isShowBrand = false
            this.cloneResult = cloneDeep(
                this.filterData()
            )
        },
        onBrandNameChange(val) {
            this.formData.brandName = val
            this.isShowBrandName = false
            this.cloneResult = cloneDeep(
                this.filterData()
            )
        }
    }
}
</script>

<style lang="scss" scoped>
    .header {
        padding: .3rem 0;
        display: flex;
        margin: -.2rem -.2rem .5rem;
        background-color: #fff;
        position: -webkit-sticky;
        position: sticky;
        top: 0;
        z-index: 1000;
        box-shadow: 0 0 10px rgba(0, 0, 0, .1);
        > div {
            flex: 1;
            text-align: center;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
        }
    }
    .list_detail {
        + .list_detail {
            margin-top: .4rem;
        }
    }
</style>
